	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>more than 1 in 4 Canadians will be affected by mental health issues;</p>\n</li>\n<li>\n<p>suicide is the leading cause of death among young adults in Canada;</p>\n</li>\n<li>\n<p>persons undergoing treatment for mental health issues require broad support; </p>\n</li>\n<li>\n<p>those close to persons affected by mental health issues are also impacted; </p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> a Liberal Government consult on and develop a comprehensive suicide prevention strategy.   (Saskatchewan)</p>","frontmatter":{"path":"/policy/suicide-prevention-strategy","date":"Tue Mar 06 2018 00:00:26 GMT-0500 (EST)","title":"Suicide Prevention Strategy"}}},"pathContext":{}}

